import React from "react";
import Table from "../../core_components/table";
import "./Pagination.css";
import XLSX from "xlsx";
import axios from "axios";

class Pagination extends React.Component {
  data = [];
  constructor() {
    super();
    axios.get("https://jsonplaceholder.typicode.com/comments").then(data => {
      console.log(data.data);
      data = data.data;
    });
    fetch("https://jsonplaceholder.typicode.com/comments")
      .then(res => res.json())
      .then(data => {
        console.log(data);
      });
    this.downloadExcel = this.downloadExcel.bind(this);
  }

  downloadExcel() {
    /* convert from array of arrays to workbook */
    var worksheet = XLSX.utils.json_to_sheet(this.data);
    var new_workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(new_workbook, worksheet, "SheetJS");

    /* save to file */
    XLSX.writeFile(new_workbook, "SheetJS.xlsx");
  }
  render() {
    return (
      <div>
        <div className="decorate">
          <button
            title="click the button to extract table in spreadsheet"
            onClick={() => this.downloadExcel()}
          >
            WorkBook
          </button>
          <Table data={this.data}></Table>
        </div>
      </div>
    );
  }
}
export default Pagination;
