import React from "react";
import "./table.css";

class Table extends React.Component {
  data = [];
  heading = [];
  ForemostLeft() {
    // alert("foremostleft page");
    this.setState({ page: 0 });
  }
  Previous() {
    // alert("previous page");
    this.setState({ page: this.state.page - 1 });
  }
  Next() {
    // alert("Next Page");
    this.setState({ page: this.state.page + 1 });
  }
  ForemostRight() {
    // alert("Foremostright page");
    this.setState({ page: 49 });
  }
  keyFunction() {
    alert("you pressed a key");
  }
  selectFunction() {
    alert("selected a value");
  }
  constructor(props) {
    super(props);
    console.log(this.data);
    this.state = {
      page: 0
    };

    this.data = this.props.data;
    if (this.data.length > 0) {
      this.heading = Object.keys(this.data[0]);
    }
    console.log(this.data);
    this.getTableData = this.getTableData.bind(this);
    this.gettheadData = this.gettheadData.bind(this);
  }

  getTableData() {
    let returnData = [];
    let initial = this.state.page * 10;
    let end = initial + 10;
    let neededData = this.data.slice(initial, end);
    returnData = neededData.map(each => {
      return (
        <tr>
          <td>{each.postId}</td>
          <td>{each.id}</td>
          <td>{each.name}</td>
          <td>{each.email}</td>
          <td>{each.body}</td>
        </tr>
      );
    });
    return returnData;
  }
  gettheadData() {
    let returnHeading = [];
    returnHeading = this.heading.map(headelement => {
      return <th>{headelement}</th>;
    });
    return returnHeading;
  }
  render() {
    let tableData = this.getTableData();
    let theadData = this.gettheadData();
    return (
      <div>
        <div className="beautify">
          <button onClick={() => this.ForemostLeft()}>&lt;&lt;</button>
          <button onClick={() => this.Previous()}>&lt;</button>
          <button>1</button>
          <button>2</button>
          <button>3</button>
          <button>4</button>
          <button>5</button>
          <button onClick={() => this.Next()}>&gt;</button>
          <button onClick={() => this.ForemostRight()}>&gt;&gt;</button>
          <label>Search: </label>{" "}
          <input type="text" onKeyUp={() => this.keyFunction()} />
          <select onChange={() => this.selectFunction()}>
            <option value="5">5</option>
            <option value="10">10</option>
            <option value="15">15</option>
            <option value="20">20</option>
          </select>
          <table className="table table-hover">
            <thead>
              <tr>{theadData}</tr>
            </thead>
            <tbody>{tableData}</tbody>
          </table>
        </div>
      </div>
    );
  }
}
export default Table;
