import React from "react";
import { Link } from "react-router-dom";
class Header extends React.Component {
  // constructor() {
  //   super();
  // }
  render() {
    return (
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <ul className="navbar-nav mr-auto">
          <li className="nav-item">
            <Link className="nav-link" to="/login">
              <i className="fas fa-sign-in-alt"></i> Login
            </Link>
          </li>

          <li className="nav-item">
            <Link className="nav-link" to="/Signup">
              <i className="fas fa-user-plus"></i> Signup
            </Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" to="/id">
              <i className="fas fa-id-badge"></i> id
            </Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" to="/identity">
              <i className="far fa-address-card"></i> identity
            </Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" to="/Pagination">
              <i className="fas fa-pager"></i> Pagination
            </Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" to="/Chart">
              <i className="fas fa-chart-pie"></i> Chart
            </Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" to="/Hello">
              <i className="fas fa-phone-square-alt"></i> Hello
            </Link>
          </li>
          {/* <li className="nav-item">
            <Link className="nav-link" to="/Table">
              <i className="fas fa-table"></i> Table
            </Link>
          </li> */}
        </ul>
      </nav>
    );
  }
}
export default Header;
