import React from "react";
import "./login.css";
import Property from "../Property/Property";
class Login extends React.Component {
  state = {
    FormError: {
      Username: "",
      Password: ""
    },
    Username: "",
    Password: ""
  };
  constructor() {
    super();
    this.ChangeInput = this.ChangeInput.bind(this);
  }
  ChangeInput(event) {
    let key = event.target.id;
    let value = event.target.value;
    let stateObj = {
      [key]: value,
      FormError: {
        Username: this.state.FormError.Username,
        Password: this.state.FormError.Password
      }
    };
    console.log(event);
    console.log(event.target);
    console.log(value);
    if (event.target.id === "Username") {
      let userRegex = new RegExp("^.{3,}$");
      stateObj.FormError.Username = userRegex.test(value)
        ? ""
        : "Username should have atleast 3 characters";
    } else {
      let strongRegex = new RegExp(
        "^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*])(?=.{8,})"
      );
      stateObj.FormError.Password = strongRegex.test(value)
        ? ""
        : "Password must have a spoecial character,one uppercase and one lowercase";
    }
    this.setState(stateObj);
  }

  hi = "hello";
  pass = "12345";
  name = "XYZ";
  number = "123";

  myFunction() {
    alert("hi");
  }
  render() {
    console.log(this.state);
    return (
      <div className="design">
        <div className="error">
          <small>{this.state.FormError.Username}</small>
          <small>{this.state.FormError.Password}</small>
        </div>
        <div className="form-group">
          <label>
            <i className="fas fa-user-tie"></i> Username
          </label>
          <input
            type="text"
            id="Username"
            value={this.state.Username}
            onChange={e => this.ChangeInput(e)}
          />
        </div>
        <div className="form-group">
          <div id="modify">
            <label>
              <i className="fas fa-lock"></i> Password
            </label>
            <input
              type="password"
              id="Password"
              value={this.state.Password}
              onChange={e => this.ChangeInput(e)}
            />
          </div>
        </div>
        <button onClick={() => this.myFunction()}>login</button>
        <Property pass={this.name}></Property>
        <Property pass={this.number}></Property>
      </div>
    );
  }
}
export default Login;
